<template>
  <div>
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <div class="btn-block">
        <div @click="showDialog()" class="btn-main-deepen">新增课程</div>
      </div>
      <search-box :searchData="searchData" :table="table"></search-box>
      <resize-table>
      <table class="table">
        <thead>
        <tr>
          <th width="25%">序号</th>
          <th width="25%">课程名称</th>
          <th width="25%">所属教研组</th>
          <th width="25%">操作</th>
        </tr></thead>
        <tbody>
        <tr v-for="(item,key) in table.list">
          <td>{{(table.query.pageNo - 1) * table.query.pageSize + key + 1}}</td>
          <td>{{item.courseName}}</td>
          <td>{{item.teachGroupName}}</td>
          <td>
            <span @click="showDialog(item)" class="oper">编辑</span>
          </td>
        </tr></tbody>
      </table>
      </resize-table>
      <no-data v-show="!table.noData"></no-data>
      <paging :data.sync="table"></paging>
    </main-part>
    <el-dialog width="540px" :visible.sync="dialog" class="dialog" :title="title">
      <div class="item">
        <span class="title imp">课程名称</span>
        <el-input
            v-show="title === '编辑课程'"
            :disabled="form.status"
            v-model="form.courseName" class="input" placeholder="请输入课程名称"></el-input>
        <el-input
            v-show="title === '新增课程'"
            v-model="form.courseName" class="input" placeholder="请输入课程名称"></el-input>
      </div>
      <div class="item">
        <span class="title imp">教研组</span>
        <el-select v-model="form.teachGroupId" default-first-option clearable filterable class="input">
          <el-option
            v-for="(item,key) in teachGroupList"
            :label="item.name" :value="item.id" :key="key"
          ></el-option>
        </el-select>
      </div>
      <div class="btns">
        <div @click="doSubmit" class="btn-submit mr15">提交</div>
        <div @click="dialog = false" class="btn-clear">取消</div>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        nav: [{name: "课程管理", path: ""}],
        searchData: [
          {type: 'input', placeholder: '课程/教研组'}
        ],
        table:{
          url: '/teachGroupCourse/queryTeachGroupCoursePage.do',
          el: '.table',
          total: 0,
          noData: true,
          list: [],
          query: {
            auditStatus: null,
            keywords: ''
          }
        },
        dialog: false,
        title: '新增课程',
        // 教研组列表
        teachGroupList: [],
        form: {
          teachGroupId: null,
          courseName: ''
        },

      }
    },
    methods: {
      // 获取所有的教研组
      async getTeachGroup(){
        this.teachGroupList = await this.$ajax({
          url: '/teachGroup/queryTeachGroupList.do'
        })
      },
      // 新增编辑课程
      async doSubmit(){
        if(!this.form.courseName){
          this.$message.warning('请输入课程名字')
          return
        }
        if(!this.form.teachGroupId){
          this.$message.warning('请选择教研组')
          return
        }
        let url = '/teachGroupCourse/addTeachGroupCourse.do'
        let text = '新增成功'
        if(this.title === '编辑课程'){
          url = '/teachGroupCourse/updateTeachGroupCourse.do'
          text = '编辑成功'
        }
        await this.$ajax({
          url,
          query: this.form
        })
        this.dialog = false
        this.$remind(text)
        this.$search(this.table)
      },
      // 展示弹窗
      showDialog(item){
        this.dialog = true
        if(item){
          this.title = '编辑课程'
          this.form = {
            status: '语文,数学,英语,物理,化学,政治,历史,地理,生物,美术,体育,音乐,美术'.indexOf(item.courseName) !== -1,
            id: item.id,
            courseId: item.courseId,
            teachGroupId: item.teachGroupId,
            courseName: item.courseName
          }
        }else{
          this.title = '新增课程'
          this.form = {
            teachGroupId: null,
            courseName: ''
          }
        }
      }
    },
    mounted(){
      this.$search(this.table)
      this.getTeachGroup()
    }
  }
</script>
<style lang="sass" scoped>
  .dialog
    .title
      width: 70px
      line-height: 40px
      vertical-align: top
      display: inline-block
    .input
      width: calc(100% - 70px)
    .item
      margin-bottom: 20px
    .btns
      text-align: center
</style>
